@media only screen and (min-width: 768px) {
#navigation {
  position: fixed;
  width: 100%;
  height: 20%;
  /* background: red; */
  z-index: 1000;
}

.navWrap {
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box; /* Opera/IE 8+ */
  height: 100%;
  padding-left: 6%;
  padding-right: 6%;
}
.leftNav {
  height: 100%;
  float: left;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex: 1;
}
.rightNav {
  height: 100%;
  float: right;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex: 1;
}
#logo {
  font-family: Inter;
  /* position: absolute; */
  font-size: 22px;
  font-weight: 400;
  /* text-transform: uppercase; */
  color: white;
}
.backBtn {
  position: absolute;
  font-size: 22px;
  font-weight: 300;
  /* text-transform: uppercase; */
  color: white;
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.navItem {
  font-size: 22px;
  font-weight: 300;
  color: white;
}
:link {
  text-decoration: none;
}

}